import React from "react";
class ShowTweets extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            tweets: []
        };
    }

    componentDidMount() {
        this.getTweets();
    }

    getTweets() {
        fetch('/api/tweets')
            .then(res => res.json())
            .then(tweets => {
                this.setState({ tweets });
            });
    }

    render() {
        return (
            <div className="container-fluid d-flex justify-content-center align-items-center" style={{
                backgroundColor: "#292F33",
                minHeight: "100vh"
            }}>
                <div className="container" style={{ maxWidth: "1100px" }}>
                    <div className="row d-flex justify-content-center align-items-center">
                        <div className="col-lg-12 align-items-center justify-content-center">
                            <div>
                                <a class="twitter-timeline"
                                    dnt="false"
                                    data-lang="tr"
                                    data-height="75vh"
                                    data-chrome="noheader noborders nofooter noscrollbar"
                                    data-theme="dark"
                                    href="https://www.twitter.com/cangokceaslan?ref_src=twsrc%5Etfw">Tweets by cangokceaslan</a>
                            </div>
                        </div>
                        {/* <div className="col-lg-6  align-items-center justify-content-center">
                            <div>
                                <a class="twitter-timeline"
                                    dnt="false"
                                    data-lang="tr"
                                    data-width="100%"
                                    data-chrome="noheader noborders nofooter noscrollbar"
                                    data-height="75vh"
                                    data-theme="dark"
                                    href="https://www.twitter.com/cangokceaslan?ref_src=twsrc%5Etfw">Tweets by cangokceaslan</a>
                            </div>
                        </div> */}
                    </div>
                </div>

            </div>
        );
    }
}
export default ShowTweets;